/* eslint-disable */
import React from 'react'
import { Button, Modal, Transition } from 'semantic-ui-react'
import { DoughnutChartJs, LineChartJs, BarChartJs } from '../chartJs/chartReact'
const statistic = {
  display: 'flex',
  justifyContent: 'space-around'
}
const iconBtn = {
  margin: 'auto !important',
  height: '46px',
  width: '46px'
}
const iconMarg = {
  marginLeft: '-8px'
}

function exampleReducer(state, action) {
  switch (action.type) {
    case 'OPEN_MODAL':
      return {
        open: true,
        visible: true,
        dimmer: action.dimmer,
        chart: action.chart
      }
    case 'CLOSE_MODAL':
      return { open: false, visible: false }
    default:
      throw new Error()
  }
}

function ModalOption() {
  const [state, dispatch] = React.useReducer(exampleReducer, {
    open: false,
    dimmer: undefined,
    chart: undefined,
    visible: false
  })
  const { open, dimmer, visible, chart } = state
  return (
    <div style={statistic}>
      <Button
        style={iconBtn}
        className="orange circular"
        onClick={() =>
          dispatch({ type: 'OPEN_MODAL', dimmer: 'inverted', chart: 'bar' })
        }>
        <i
          aria-hidden="true"
          className="chart bar large icon"
          style={iconMarg}
        />
      </Button>
      <Button
        style={iconBtn}
        className="purple circular"
        onClick={() =>
          dispatch({ type: 'OPEN_MODAL', dimmer: 'inverted', chart: 'line' })
        }>
        <i
          aria-hidden="true"
          className="chart line large icon"
          style={iconMarg}
        />
      </Button>
      <Button
        style={iconBtn}
        className="olive circular"
        onClick={() =>
          dispatch({
            type: 'OPEN_MODAL',
            dimmer: 'inverted',
            chart: 'doughnut'
          })
        }>
        <i
          aria-hidden="true"
          className="chart pie large icon"
          style={iconMarg}
        />
      </Button>

      <Transition visible={visible} animation="fade up" duration={600}>
        <Modal
          dimmer={dimmer}
          open={open}
          onClose={() => dispatch({ type: 'CLOSE_MODAL' })}>
          {chart == 'line' ? (
            <LineChartJs />
          ) : chart == 'bar' ? (
            <BarChartJs />
          ) : chart == 'doughnut' ? (
            <DoughnutChartJs />
          ) : (
            ''
          )}
        </Modal>
      </Transition>
    </div>
  )
}

export default ModalOption
