/* eslint-disable */
import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom'
import { Button, Form } from 'semantic-ui-react'
import { connect, useDispatch } from 'react-redux'

import Layout from './Layout'
import { setAuthenticated } from '../../actions/actions'
import './auth.css'

// eslint-disable-next-line react/prop-types
const Login = ({ isAuthenticated, realEmail, realPassword }) => {
  const history = useHistory()
  const dispatch = useDispatch()

  const [email, setEmail] = useState('')
  const [password, setPassword] = useState('')

  useEffect(() => {
    if (isAuthenticated) {
      history.push('/dashboard')
    }
  }, [isAuthenticated])

  const handleAuth = () => {
    if (realEmail === email && realPassword === password) {
      dispatch(setAuthenticated(true))
      history.push('/dashboard')
    } else {
      // eslint-disable-next-line no-console
      console.log('credentials error')
    }
  }

  return (
    <Layout header="">
      <Form.Input
        fluid
        icon="user"
        iconPosition="left"
        placeholder="E-mail"
        className="auth-input-field"
        value={email}
        onChange={event => {
          setEmail(event.target.value)
        }}
      />
      <Form.Input
        fluid
        icon="lock"
        iconPosition="left"
        placeholder="Mot de passe"
        type="password"
        className="auth-input-field"
        value={password}
        onChange={event => setPassword(event.target.value)}
      />

      <Button primary fluid size="huge" onClick={handleAuth}>
        Connexion
      </Button>
    </Layout>
  )
}
const mapStateToProps = state => ({
  isAuthenticated: state.placesControls.isAuthenticated,
  realEmail: state.placesControls.email,
  realPassword: state.placesControls.password
})
export default connect(mapStateToProps)(Login)
