/* eslint-disable */
import React from 'react'
import { Card, Feed, Statistic } from 'semantic-ui-react'

const imgStyles = {
  width: '72px',
  height: '72px'
}
const center = {
  justifyContent: 'center',
  display: 'grid'
}
const Ouvrage = () => (
  <Card>
    <Card.Content>
      <Card.Header>Ouvrages</Card.Header>
    </Card.Content>
    <Card.Content>
      <Feed>
        <Feed.Event>
          <Feed.Label image="./map-marker_tankWater.png" style={imgStyles} />
          <Feed.Content style={center}>
            <Feed.Date content="Reservoir" />
            <Feed.Summary>
              <Statistic>
                <Statistic.Value>
                  <a>22</a>
                </Statistic.Value>
              </Statistic>
            </Feed.Summary>
          </Feed.Content>
        </Feed.Event>

        <Feed.Event>
          <Feed.Label image="./map-marker_well.png" style={imgStyles} />
          <Feed.Content style={center}>
            <Feed.Date content="Forage" />
            <Feed.Summary>
              <Statistic>
                <Statistic.Value>
                  <a>32</a>
                </Statistic.Value>
              </Statistic>
            </Feed.Summary>
          </Feed.Content>
        </Feed.Event>
        <Feed.Event>
          <Feed.Label image="./map-marker_puit.png" style={imgStyles} />
          <Feed.Content style={center}>
            <Feed.Date content="Puit" />
            <Feed.Summary>
              <Statistic>
                <Statistic.Value>
                  <a>22</a>
                </Statistic.Value>
              </Statistic>
            </Feed.Summary>
          </Feed.Content>
        </Feed.Event>
        <Feed.Event>
          <Feed.Label image="./map-marker_tank.png" style={imgStyles} />
          <Feed.Content style={center}>
            <Feed.Date content="Chateau" />
            <Feed.Summary>
              <Statistic>
                <Statistic.Value>
                  <a>64</a>
                </Statistic.Value>
              </Statistic>
            </Feed.Summary>
          </Feed.Content>
        </Feed.Event>
      </Feed>
    </Card.Content>
  </Card>
)

export default Ouvrage
