/* eslint-disable */
import React, { useEffect, useState } from 'react'
import { connect, useDispatch, useSelector } from 'react-redux'
import L from 'leaflet'
import proj4 from 'proj4'
import 'proj4leaflet'
// import { antPath } from 'leaflet-ant-path'
// import route from '../route'
import Slidebar from '../components/Dimmer/slidebar'
import 'Leaflet.TileLayer.MBTiles'
import 'leaflet.gridlayer.googlemutant'
import '../leaflet-geojson-vt'
import 'leaflet-sidebar'
import 'leaflet-sidebar/src/L.Control.Sidebar.css'
import 'leaflet.fullscreen/Control.FullScreen'
import 'leaflet.fullscreen/Control.FullScreen.css'
import 'leaflet-easybutton'
import 'leaflet-easyprint'
import 'leaflet-easybutton/src/easy-button.css'
import {
  bright,
  satelliteGoogle,
  standard,
  openStreet,
  topographique,
  blancPage,
  esri,
  ENVIRONMENT,
  PLANNING_CADASTRE,
  FORAGES_1,
  gis_osm_waterways_free_1
} from '../leafletLayer'

import {
  getMarkerIcon,
  getPopUpInfo,
  getbindTooltip,
  polyOptions
} from './utils'
import { doUpdateBoundingBox } from '../actions/actions'
import anime from 'animejs/lib/anime.es.js'

// import '../L.Path.DashFlow'
// eslint-disable-next-line import/no-duplicates
//

// tryThis2.on('databaseloaded', ev => {
//   // console.info('MBTiles DB loaded', ev)
// })
// tryThis2.on('databaseerror', ev => {
//   // console.info('MBTiles DB error', ev)
// })
const testBatiSHP = require('../testBatiSHP.json')
const polylineWather = require('../watherPolyline.json')
const baseMaps = {
  'Topographique: Topographique Map': topographique,
  'Streets Map: Streets Map': openStreet,
  'Standard: page blanche': blancPage,
  'Standard: Standard': standard,
  'Bright: Bright': bright,
  'Satellite : Satellite': satelliteGoogle,
  'Satellite : ESRI ': esri
}

const options = {
  // tileSize: 256.00009,
  maxZoom: 24,
  tolerance: 3,
  debug: 0,
  style: {
    fillColor: '#ffff0047',
    color: 'black',
    weight: 1
  }
  // className: 'svgPossition'
}

const bati = L.gridLayer.geoJson(testBatiSHP, options)
const line = L.gridLayer.geoJson(polylineWather, options)

// const placesLayer = L.featureGroup()
// const clusterLayer = L.featureGroup()

// const line = new L.Polyline.AntPath(polylineWather, {
//   dashArray: '15 15',
//   dashSpeed: 30
// })

// const antPolyline = L.polyline.antPath(polylineWather)
const overlayMaps = {
  // 'Points of interest': placesLayer,
  'Points de batiment': bati,
  'Points de rseau': line,
  ENVIRONMENT: ENVIRONMENT,
  PLANNING_CADASTRE: PLANNING_CADASTRE,
  'BOUIRA FORAGES_1': FORAGES_1,
  gis_osm_waterways_free_1: gis_osm_waterways_free_1
}
// SWEREF99 TM (EPSG:3006) with map's pixel origin at SWEREF99 TM coordinate (0, 0)

// const crs = new L.Proj.CRS('EPSG:3006',
//   '+proj=utm +zone=33 +ellps=GRS80 +towgs84=0,0,0,0,0,0,0 +units=m +no_defs',
//   {
//     resolutions: [
//       8192, 4096, 2048, 1024, 512, 256, 128
//     ],
//     origin: [0, 0]
//   })
// console.log('crs:',crs)
proj4.defs(
  'urn:ogc:def:crs:OGC:1.3:CRS84',
  '+proj=utm +zone=15 +ellps=GRS80 +datum=NAD83 +units=m +no_defs'
)

const mapParams = {
  center: [31.159172882830028, -1.5921003374714449],
  zoomControl: true,
  maxBounds: L.latLngBounds(L.latLng(-90, -180), L.latLng(90, 180)),
  zoom: 13,
  layers: [topographique, openStreet],
  fullscreenControl: true,
  fullscreenControlOptions: {
    position: 'topright'
  }
}

// eslint-disable-next-line react/prop-types
const OurMap = ({ filters, coordones }) => {
  const makeAPICall = async () => {
    try {
      const response = await fetch('http://vps7f98c3.oct-xpd1.xyz/', {
        mode: 'cors'
      })
      const data = await response
      console.log({ data })
    } catch (e) {
      console.log(e)
    }
  }
  const dispatch = useDispatch()
  const mapRef = React.useRef(null)
  const [mapMarkers, setMapMarkers] = useState([])

  const filtredMarkers = useSelector(state => {
    // eslint-disable-next-line react/prop-types
    if (filters.length === 0) {
      return state.placesControls.markers
    }
    return state.placesControls.markers.filter(marker =>
      // eslint-disable-next-line react/prop-types
      filters.includes(marker.type)
    )
  })
  useEffect(() => {
    makeAPICall()
    mapRef.current = L.map('map', mapParams)
    // const path = antPath(route, {
    //   delay: 151,
    //   dashArray: [1, 28],
    //   weight: 4,
    //   color: '#0000FF',
    //   pulseColor: '#FFFFFF',
    //   paused: false,
    //   reverse: false,
    //   hardwareAccelerated: true
    // })

    // mapRef.current.addLayer(path)
    // mapRef.current.fitBounds(path.getBounds())

    // const antPolyline = L.polyline.antPath(polylineWather, pathye)
    // antPolyline.addTo(mapRef.current)
    const clusterPane = mapRef.current.createPane('clusterPane')
    clusterPane.style.opacity = 0.9

    // we do want a zoom control
    //and for the sake of advertising your company, you may add a logo to the map
    const hereLogo = L.control({
      position: 'bottomright'
    })
    const sidebarRight = L.control.sidebar('sidebar-right', {
      closeButton: true,
      position: 'right'
    })
    mapRef.current.addControl(sidebarRight)
    const sidebarLeft = L.control.sidebar('sidebar-left', {
      closeButton: true,
      position: 'left'
    })
    mapRef.current.addControl(sidebarLeft)

    L.easyPrint({
      title: 'print',
      position: 'bottomleft',
      sizeModes: ['A4Portrait', 'A4Landscape']
    }).addTo(mapRef.current)
    L.easyButton('<div><i class="filter large icon"></i></div>', () => {
      sidebarLeft.toggle()
    })
      .addTo(mapRef.current)
      .setPosition('bottomleft')
    L.easyButton('<div><i class="clipboard large icon"></i></div>', () => {
      sidebarRight.toggle()
    })
      .addTo(mapRef.current)
      .setPosition('bottomleft')
    // L.Proj.geoJson(testBatiSHP).addTo(mapRef.current);
    // +++++++++++
    setTimeout(() => {
      // sidebarRight.show()
      sidebarRight.show()
    }, 4300)
    mapRef.current.on('click', () => {
      sidebarRight.hide()
      sidebarLeft.hide()
    })
    L.control.polylineMeasure(polyOptions).addTo(mapRef.current)
    L.control.layers(baseMaps, overlayMaps).addTo(mapRef.current)

    hereLogo.onAdd = function() {
      const div = L.DomUtil.create('div')
      div.innerHTML =
        '<a href="https://nasrosoft.me/" target="_blank" style="z-index: 1;"><div class="wrap"><div class="here-logo"><div></div></a>'
      return div
    }
    // map.addControl(brand)
    mapRef.current.addControl(hereLogo)

    mapRef.current.on('polylinemeasure:toogle', _ => {
      /* e.sttus */
    })
    mapRef.current.on('polylinemeasure:start', currentLine => {})
    mapRef.current.on('polylinemeasure:resume', currentLine => {})
    mapRef.current.on('polylinemeasure:finish', currentLine => {})
    mapRef.current.on('polylinemeasure:clear', _ => {})
    mapRef.current.on('polylinemeasure:add', _ => {
      /* e.latlng */
    })
    mapRef.current.on('polylinemeasure:insert', _ => {
      /* e.latlng */
    })
    mapRef.current.on('polylinemeasure:move', _ => {
      /* e.latlng ; e.sourceTarget._latlng */
    })
    mapRef.current.on('polylinemeasure:remove', _ => {
      /* e.latlng ; e.sourceTarget._latlng */
    })
    mapRef.current.on('moveend', () => {
      dispatch(doUpdateBoundingBox(mapRef.current.getBounds()))
    })

    dispatch(doUpdateBoundingBox(mapRef.current.getBounds()))
  }, [])
  useEffect(() => {
    mapRef.current.flyTo(coordones, 14)
  }, [coordones])
  useEffect(() => {
    // sidebar.setContent('test <b>test</b> test')
    mapMarkers.map(marker => mapRef.current.removeLayer(marker))
    filtredMarkers.map(markerData => {
      const marker = L.marker([markerData.lat, markerData.lng], {
        // eslint-disable-next-line no-undef
        icon: getMarkerIcon(markerData.iconType)
      })
      marker.addTo(mapRef.current)
      marker.bindPopup(getPopUpInfo(markerData), {
        width: '283px'
      })
      marker.bindTooltip(getbindTooltip(markerData))
      mapMarkers.push(marker)
      marker._icon.classList.add('anime')
    })
    setMapMarkers(mapMarkers)
    const markerAnime = document.querySelectorAll('.anime')
    anime({
      targets: markerAnime,
      translateY: [-15, 10], // from 100 to 250
      delay: 500
    })
  }, [filters])

  return (
    <div>
      <Slidebar />
      <div id="map" style={style} />
    </div>
  )
}
const style = {
  width: '100%',
  height: '100vh'
}
const mapStateToProps = state => ({
  markers: state.placesControls.markers,
  filters: state.placesControls.filters,
  message: state.placesControls.message,
  coordones: state.placesControls.coordones
})
export default connect(mapStateToProps)(OurMap)
