const getRandomArray = numItems => {
  const names = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'.substr(0, numItems).split('')
  return names.map(name => {
    return { label: name, value: Math.round(20 + 80 * Math.random()) }
  })
}

const getRandomDateArray = numItems => {
  const baseTime = new Date('2021-01-05T00:00:00').getTime()
  const dayMs = 24 * 60 * 60 * 1000
  const data = []
  for (let i = 0; i < numItems; i++) {
    data.push({
      time: new Date(baseTime + i * dayMs),
      value: Math.round(20 + 80 * Math.random())
    })
  }
  return data
}

export default () => {
  return [
    {
      title: 'Nouvelle branchement',
      data: getRandomDateArray(90)
    },

    {
      title: 'Refoulement',
      data: getRandomArray(20)
    },

    {
      title: 'Distribution',
      data: getRandomArray(6)
    },
    {
      title: 'Fuite',
      data: getRandomArray(8)
    }
  ]
}
