/* eslint-disable */
import React from 'react'
import {
  Segment,
  Header,
  Container,
  Divider,
  Placeholder
} from 'semantic-ui-react'
import Ouvrage from './ouvrages'

const Slidebar = () => {
  return (
    <div>
      <Segment.Group id="sidebar-right">
        <Container>
          <Header as="h2">
            <Header.Content>
              <a className="ui yellow label big">08) Unite de Bechar</a>
              <Header.Subheader>
                <br />
                <a className="ui red circular label">1</a>
                <div className="ui label">
                  Grez vos prfrences
                  <div className="detail" />
                </div>
              </Header.Subheader>
            </Header.Content>
          </Header>
        </Container>
        <Segment.Group piled>
          <Ouvrage />
        </Segment.Group>
        <br />
        <Segment>
          <Header as="h3" />
          <Placeholder>
            <Placeholder>
              <Placeholder.Header image>
                <Placeholder.Line />
                <Placeholder.Line />
              </Placeholder.Header>
            </Placeholder>
          </Placeholder>

          <Divider section />

          <Header as="h3" />
          <Placeholder>
            <Placeholder.Header image>
              <Placeholder.Line />
              <Placeholder.Line />
            </Placeholder.Header>
          </Placeholder>
          <Divider section />
        </Segment>
        <br />
        <Placeholder>
          <Placeholder.Line />
          <Placeholder.Line />
          <Placeholder.Line />
          <Placeholder.Line />
          <Placeholder.Line />
        </Placeholder>
      </Segment.Group>

      <span id="sidebar-left">
        <Container>
          <p>Bientt disponible...</p>
        </Container>
      </span>
    </div>
  )
}

export default Slidebar
