import { combineReducers } from 'redux'
import markers from '../markers'
import {
  REQUEST_PLACES_RESULTS,
  RECEIVE_PLACES_RESULTS,
  UPDATE_BBOX,
  UPDATE_DBSCAN_SETTINGS,
  COMPUTE_DBSCAN,
  RESULT_HANDLER,
  CLEAR,
  DISABLE_PLACES,
  SET_FILTER,
  SET_MARKERS,
  SET_AUTHENTICATED,
  SET_ACTIVATED,
  SET_FLY,
  SET_COLLAPSED,
  SET_SELECTED,
  OPEN_MODAL,
  CLOSE_MODAL
} from '../actions/actions'

const initialPlacesState = {
  boundingbox: '',
  message: { receivedAt: 0 },
  lastCall: Date.now(),
  places: markers,
  markers: markers,
  filtredMarkers: markers,
  dbscanSettings: {
    minPoints: 10,
    maxDistance: 500
  },
  lastCompute: 0,
  filters: [],
  coordones: [31.6099771, -2.231534],
  isAuthenticated: false,
  collapsed: false,
  selected: 'home',
  active: false,
  open: false,
  dimmer: undefined,
  email: '',
  password: ''
}

const placesControls = (state = initialPlacesState, action) => {
  switch (action.type) {
    case SET_FILTER: {
      const filters = [...state.filters, action.filter]
      if (!state.filters.includes(action.filter)) {
        return {
          ...state,
          filters
        }
      }
      return state
    }
    case SET_MARKERS:
      return {
        ...state,
        markers: action.markers
      }
    case SET_AUTHENTICATED:
      return {
        ...state,
        isAuthenticated: action.value
      }
    case SET_COLLAPSED:
      return {
        ...state,
        collapsed: action.value
      }
    case SET_SELECTED:
      return {
        ...state,
        selected: action.value
      }
    case SET_ACTIVATED:
      return {
        ...state,
        active: action.value
      }
    case SET_FLY:
      return {
        ...state,
        coordones: action.coordones
      }
    case OPEN_MODAL:
      return { ...state, open: true, dimmer: action.dimmer }
    case CLOSE_MODAL:
      return { ...state, open: false }
    case DISABLE_PLACES: {
      return {
        ...state,
        places: {
          ...state.places,
          [action.payload]: {
            ...state.places[action.payload],
            disabled: true
          }
        }
      }
    }
    case CLEAR: {
      return {
        ...state,
        filters: [],
        filtredMarkers: markers
      }
    }
    case REQUEST_PLACES_RESULTS: {
      return {
        ...state,
        places: {
          ...state.places,
          [action.payload.category]: {
            ...state.places[action.payload.category],
            isFetching: true
          }
        }
      }
    }

    case RECEIVE_PLACES_RESULTS: {
      return {
        ...state,
        lastCall: Date.now(),
        places: {
          ...state.places,
          [action.payload.category]: {
            ...state.places[action.payload.category],
            data: state.places[action.payload.category].hasOwnProperty('data')
              ? [
                  ...state.places[action.payload.category].data,
                  ...action.payload.data.items
                ]
              : action.payload.data.items,
            next: action.payload.data.next,
            previous: action.payload.data.previous,
            boundingbox: action.payload.boundingbox,
            color: action.payload.color,
            isFetching: false,
            disabled: !action.payload.data.next
          }
        }
      }
    }
    case UPDATE_BBOX: {
      const newState = { ...state }
      newState.boundingbox = action.payload
      for (const obj in newState.places) {
        newState.places[obj].disabled = false
        newState.places[obj].next = undefined
        newState.places[obj].previous = undefined
      }

      return newState
    }
    case RESULT_HANDLER: {
      return {
        ...state,
        message: {
          ...state.message,
          ...action.payload,
          receivedAt: Date.now()
        }
      }
    }
    case COMPUTE_DBSCAN: {
      return {
        ...state,
        lastCompute: Date.now()
      }
    }
    case UPDATE_DBSCAN_SETTINGS: {
      return {
        ...state,
        dbscanSettings: {
          ...state.dbscanSettings,
          [action.payload.setting]: action.payload.value
        }
      }
    }
    default: {
      return state
    }
  }
}

const rootReducer = combineReducers({
  placesControls
})

export default rootReducer
