/* eslint-disable */
import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { SemanticToastContainer, toast } from 'react-semantic-toasts'
import AccordionExampleMenu from './AccordionMenu'
import { Accordion } from 'semantic-ui-react'
import {
  Segment,
  Divider,
  Button,
  Label,
  Popup,
  Transition
} from 'semantic-ui-react'
import { setFilter, computeDbScan, clear, setFly } from '../actions/actions'
import herePlaces from '../adePlaces'

const buttonsStyle = {
  textAlign: 'center'
}
const segmentStyle = {
  zIndex: 997,
  position: 'absolute',
  width: '324px',
  top: '47px',
  left: '10px',
  maxHeight: 'calc(100vh - 3vw)',
  overflow: 'auto',
  padding: '20px'
}
const segmentStyleBtn = {
  zIndex: 999,
  position: 'absolute',
  width: '324px',
  top: '10px',
  left: '10px',
  height: '46px',
  padding: '20px',
  display: 'flex',
  justifyContent: 'center',
  backgroundColor: 'white'
}
const btnStyle = {
  backgroundColor: 'white',
  width: '241px',
  marginTop: '-17px',
  height: '41px'
}
const icons = {
  float: 'left'
}
const iconsDown = {
  float: 'right',
  marginTop: '-13px'
}
const btnCenter = {
  display: 'flex',
  justifyContent: 'center'
}
const btnPading = {
  padding: '0 2px'
}

const CustomLabel = ({ content, value }) => (
  <Popup content={content} trigger={<Label size="tiny">{value}</Label>} />
)
CustomLabel.propTypes = {
  content: PropTypes.string,
  value: PropTypes.string
}

class Control extends React.Component {
  static propTypes = {
    places: PropTypes.array,
    boundingbox: PropTypes.string,
    dispatch: PropTypes.func.isRequired,
    message: PropTypes.object,
    filters: PropTypes.array
  }

  handleClick = (type, coordones) => {
    const { dispatch } = this.props
    dispatch(setFilter(type))
    dispatch(setFly(coordones))
  }
  // handleClick = type => {
  //   const { dispatch } = this.props
  //   // eslint-disable-next-line no-console
  //   console.log('dispatching:', type)
  //   dispatch(setFilter(type))
  // }

  handleClickDbscan = () => {
    const { dispatch } = this.props
    dispatch(computeDbScan())
  }

  handleClickClear = () => {
    const { dispatch } = this.props
    dispatch(clear())
  }
  componentDidUpdate = prevProps => {
    const { message } = this.props

    if (message.receivedAt > prevProps.message.receivedAt) {
      toast({
        type: message.type,
        icon: message.icon,
        title: message.topic,
        description: message.description,
        time: 5000
      })
    }
  }

  areButtonsDisabled = places => {
    let buttonsDisabled = true
    for (const key in places) {
      if (places.hasOwnProperty(key)) {
        if (
          places[key].hasOwnProperty('type') &&
          !this.props.filters.includes(places[key].type)
        ) {
          buttonsDisabled = false
        }
      }
    }

    return buttonsDisabled
  }
  state = { visible: false, filtredPlaces: herePlaces }

  toggleVisibility = () =>
    this.setState(prevState => ({ visible: !prevState.visible }))
  render() {
    const { places } = this.props
    const CustomButton = ({
      content,
      circular,
      popupContent,
      handler,
      name,
      icon,
      value,
      disabled,
      basic,
      size,
      loading,
      color
    }) => (
      <Popup
        content={popupContent}
        size={size}
        trigger={
          <Button
            color={color}
            circular={circular}
            content={content}
            loading={loading}
            name={name}
            size={size}
            onClick={handler}
            basic={basic}
            disabled={disabled}
            icon={icon}
          />
        }
      />
    )
    const Level1Content = (
      <div>
        {Object.keys(herePlaces).map((key, index) => {
          let content = herePlaces[key].name
          if (places[herePlaces[key].type]) {
            if (
              !places[herePlaces[key].type].disabled &&
              places[herePlaces[key].type].next
            ) {
              content = 'chercher plus ..'
            }
          }
          return (
            <div
              key={index}
              className="eight column row mt1 dib"
              style={btnPading}>
              <CustomButton
                icon={false}
                popupContent={'Afficher des lieux de cette catgorie'}
                content={content}
                name={herePlaces[key].type}
                handler={() =>
                  this.handleClick(
                    herePlaces[key].name,
                    herePlaces[key].coordones
                  )
                }
                // handler={() => this.handleClick(herePlaces[key].type)}
                color={herePlaces[key].color}
                disabled={
                  places[herePlaces[key].type]
                    ? places[herePlaces[key].type].disabled
                    : false
                }
                loading={
                  places[herePlaces[key].type]
                    ? places[herePlaces[key].type].isFetching
                    : false
                }
                size="small"
              />
            </div>
          )
        })}
      </div>
    )
    const rootPanels = [
      {
        key: 'panel-1',
        title: '08) Unite de Bechar',
        content: { content: Level1Content }
      }
    ]

    const { visible } = this.state
    return (
      <div className="tool-group bg-light" id="toolGroup">
        <Segment style={segmentStyleBtn}>
          <div
            className="ui animated fade button"
            onClick={this.toggleVisibility}
            style={btnStyle}>
            <i className="low vision icon" style={icons} />
            <div className="visible content">Filtrer les donnes</div>
            <div className="hidden content">Masquer le filtrage</div>
            <i className="sitemap icon" style={iconsDown} />
          </div>
          <Divider hidden />
        </Segment>
        <Transition visible={visible} animation="fade up" duration={600}>
          <Segment style={segmentStyle}>
            <div style={buttonsStyle}>
              <Accordion defaultActiveIndex={1} panels={rootPanels} styled />

              <Divider />
              <AccordionExampleMenu />
              <Divider />
              <div className="mt2" style={btnCenter}>
                <Button
                  icon={'refresh'}
                  size={'large'}
                  popupcontent={'Rinitialiser les lieux et la carte'}
                  onClick={this.handleClickClear}
                  disabled={false}
                  className="circular"
                />
              </div>
            </div>
          </Segment>
        </Transition>
        <SemanticToastContainer position="bottom-center" />
      </div>
    )
  }
}

const mapStateToProps = state => {
  const { places, boundingbox, message, filters } = state.placesControls
  return {
    places,
    boundingbox,
    message,
    filters
  }
}

export default connect(mapStateToProps)(Control)
