import L from 'leaflet'

export const getbindTooltip = marker => {
  return `<div class='swing up'>
  <h4>${marker.lable}</h4>`
}
export const getProgress = marker => {
  let color

  switch (marker.status) {
    case 'bon':
      color = '#21ba45'
      break
    case 'moyenne':
      color = '#FCB941'
      break
    case 'mauvais':
      color = '#DB2828'
  }

  return `
  <style>
  .bar {
    animation: barmove 1s;
  }
  
  @keyframes barmove {
    from {
      width: 0%;
    }
    to {
      width: ${marker.width}%;
    }
  }
  </style>
  <div class="bar" style='width: ${marker.width}%;
  min-width: 0%;
  background: ${color} !important; margin-top: 12px;'>
  </div>
  <div class="label"style='top: 3%;
  color: #fff;'>${marker.status}</div>
</div>
`
}
export const getMarkerIcon = iconType => {
  switch (iconType) {
    case 'well':
      return L.icon({
        iconUrl: 'map-marker_well.png',
        iconSize: [38, 38]
      })
    case 'puit':
      return L.icon({
        iconUrl: 'map-marker_puit.png',
        iconSize: [38, 38]
      })
    case 'tank':
      return L.icon({
        iconUrl: 'map-marker_tank.png',
        iconSize: [38, 38]
      })
    case 'tankWater':
      return L.icon({
        iconUrl: 'map-marker_tankWater.png',
        iconSize: [38, 38]
      })
  }
}

