import _ from 'lodash'
// import faker from 'faker'
import React, { Component } from 'react'
import { Button, Dropdown } from 'semantic-ui-react'
import herePlaces from '../../adePlaces'

const getRandomColor = () => {
  const colors = []
  Object.keys(herePlaces).map((key, index) => {
    colors.push(herePlaces[key].color)
  })
  // eslint-disable-next-line no-console
  const random_color = colors[Math.floor(Math.random() * colors.length)]
  return random_color
}
const getOptions = () =>
  _.times(3, () => {
    const name = getRandomColor()
    return {
      key: name,
      text: name,
      value: _.snakeCase(name),
      color: getRandomColor()
    }
  })

class DropdownExampleRemote extends Component {
  state = {
    isFetching: false,
    multiple: true,
    search: true,
    searchQuery: null,
    value: [],
    options: getOptions()
  }

  handleChange = (e, { value }) => this.setState({ value })
  handleSearchChange = (e, { searchQuery }) => this.setState({ searchQuery })

  fetchOptions = () => {
    this.setState({ isFetching: true })

    setTimeout(() => {
      this.setState({ isFetching: false, options: getOptions() })
      this.selectRandom()
    }, 500)
  }

  selectRandom = () => {
    const { multiple, options } = this.state
    const value = _.sample(options).value
    this.setState({ value: multiple ? [value] : value })
  }

  toggleSearch = e => this.setState({ search: e.target.checked })

  toggleMultiple = e => {
    const { value } = this.state
    const multiple = e.target.checked
    // convert the value to/from an array
    const newValue = multiple ? _.compact([value]) : _.head(value) || ''
    this.setState({ multiple, value: newValue })
  }

  render() {
    const { multiple, options, isFetching, search, value } = this.state

    return (
      <div>
        <div width={8}>
          <p className="layerRandom">
            <Button onClick={this.fetchOptions}>Fetch</Button>
            <Button onClick={this.selectRandom} disabled={_.isEmpty(options)}>
              Random
            </Button>
          </p>
          <Dropdown
            fluid
            selection
            multiple={multiple}
            search={search}
            options={options}
            value={value}
            placeholder="Search"
            onChange={this.handleChange}
            onSearchChange={this.handleSearchChange}
            disabled={isFetching}
            loading={isFetching}
            renderLabel={option => ({
              color: option.color,
              content: option.text
            })}
          />
        </div>
      </div>
    )
  }
}

export default DropdownExampleRemote
